export default {
	name: 'temHome',
	props:{
		msg: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			
		};
	},
	onLoad() {
		
	},
	methods:{
		gotoChild(type) {
			switch (type) {
				case 'task':
					uni.navigateTo({
						url: 'task/task'
					});
					
					break;
				case 'todo':
					uni.navigateTo({
						url: 'dealList/dealList'
					});
					break;
				case 'report':
					uni.navigateTo({
						url: 'report/report'
					});
					break;
				case 'file':
				uni.navigateTo({
					url: 'sacnCode/sacnCode'
				})
					break;
			}
		},
	}
}
